import React, { useState, useEffect } from 'react'
import {
  CBadge,
  CCard,
  CCardBody,
  CCardHeader,
  CCol,
  CRow,
  CFormGroup
} from  '@coreui/react'
import CIcon from '@coreui/icons-react'
import { httpClient } from '../../helpers';
import ReactJson from 'react-json-view'

const Transactions = (props) => {
    const [fetch, setFetch] = useState(true);
    const [transaction, setTransaction] = useState(true);

    useEffect(() => {
        if(fetch) {
          const url = "/be/api/transaction/data/" + props.match.params.id ;
          httpClient
            .get(url)
            .then((res) => {
                setTransaction(res.data);
            })
            .catch((error) => {
              console.error("Error", error);
            });
          setFetch(false);
        }
      },[fetch]);

  return (
    <>
      <CRow>
        <CCol xs="12" sm="6" md="4">
          <CCard>
            <CCardHeader>
              Transaction
              <div className="card-header-actions">
                <CBadge color="success" className="float-right">{transaction.identifier}</CBadge>
              </div>
            </CCardHeader>
            <CCardBody>
                <CFormGroup>
                <div>
                    <div className="small text-muted">Created At</div>
                    <strong>{transaction.created_at}</strong>
                </div>
                </CFormGroup>
                <CFormGroup>
                <div>
                    <div className="small text-muted">Client Code</div>
                    <strong>{transaction.clientcode}</strong>
                </div>
                </CFormGroup>
                <CFormGroup>
                <div>
                    <div className="small text-muted">File Name</div>
                    <strong>{transaction.filename}</strong>
                </div>
                </CFormGroup>
            </CCardBody>
          </CCard>
        </CCol>
        <CCol xs="12" sm="6" md="6">
          <CCard>
            <CCardHeader>
              Data
            </CCardHeader>
            <CCardBody>
              <ReactJson src={transaction.jsonout} />
            </CCardBody>
          </CCard>
        </CCol>
      </CRow>
    </>
  )
}

export default Transactions
